<template>
 <div class="wrp" ref="wrapper">
  <slot></slot>
 </div>
</template>

<script>
    import BScroll from 'better-scroll'

    export default {
      name: "ScrollContent",
      props:{
        //probeType probeType  1 scroll 
        //  probeType  2  scroll 
        //  probeType  3  momentum  scroll 
        //  0 scroll 
        probeType: {
          type: Number,
          default: 3
        },
        // pullUpLoad
        //  false true  Object 
        pullUpLoad: {
          type: Boolean,
          default: true
        }
      },
      data(){
        return{
          bs: {}
        }
      },
      mounted() {
        setTimeout(this.__initScroll, 20)
      },
      methods: {
        __initScroll() {
          if (!this.$refs.wrapper) {
            return
          }
          //BSscroll
          this.bs = new BScroll(this.$refs.wrapper, {
            probeType: this.probeType,
            click:true,
            pullUpLoad:this.pullUpLoad
          })

          // console.log(this.$refs)
          this.bs.on('scroll', pos=>{
            this.$emit("bsPosition",pos)
          })

          this.bs.on('pullingUp',()=>{
            this.pullUpCallBack();
          })

        },
        pullUpCallBack(){
          console.log('');
          this.$emit('pullingUpDoing',true)
        },
        refresh() {
          this.bs && this.bs.refresh && this.bs.refresh()
        },
        finishPullUp(){
          this.bs && this.bs.finishPullUp && this.bs.finishPullUp()
        },

        toTop(x,y,sTime){
          this.bs && this.bs.scrollTo && this.bs.scrollTo(x,y,sTime)

        },
        getScrollHeight(){
          return this.bs? this.bs.y : 0
        }


      }
    }
</script>

<style scoped>
  .wrp{
    width: 100%;
    overflow: hidden;
    position: absolute;
    top: 50px;
    bottom: 49px;
    left: 0;
    right: 0;
  }
</style>
